import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormControl, FormGroup } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { Observable, Subscription } from 'rxjs';
import { debounceTime } from 'rxjs/operators';
import { RetailerCompanyService } from 'src/app/shared/services/retailer-company.service';
import { RetailerCompany } from 'src/app/shared/models/retailer-company';

@Component({
  selector: 'app-retailer-company-left-pane',
  templateUrl: './retailer-company-left-pane.component.html',
  styleUrls: ['./retailer-company-left-pane.component.scss']
})
export class RetailerCompanyLeftPaneComponent implements OnInit {

  advancedSearchSub: Subscription;

  companies: RetailerCompany[];
  filteredCompanies: RetailerCompany[];
  loadDataSub: Subscription;
  selectedAction = 'All';
  loadingCollection = false;
  advancedSearchApplied = false;

  page = 1;
  pageSize = 5;
  collectionSize = 0;

  searchControl: FormControl = new FormControl();

  filterAction: string[] = ['All', 'Active', 'Inactive'];

  advancedSearchForm: FormGroup;
  searchedCompanies: Observable<RetailerCompany[]>;

  public userInput = new FormControl();

  constructor(private companyService: RetailerCompanyService,
    private router: Router,
    private route: ActivatedRoute,
    private modalService: NgbModal,
    private fb: FormBuilder) {
  }


  ngOnInit(): void {
    this.setAdvancedSearchForm();

    this.loadDataSub = this.companyService.loadRetailerCompanyData()
      .subscribe(res => {
        this.companies = this.companyService.companies;
        this.filteredCompanies = this.companyService.companies;
        if (this.filteredCompanies) {
          this.collectionSize = this.filteredCompanies.length;
        }
      });

    this.searchControl.valueChanges
      .pipe(debounceTime(200))
      .subscribe(value => {
        this.filerData(value);
        if (this.filteredCompanies) {
          this.collectionSize = this.filteredCompanies.length;
        }
    });
  }

  filerData(val) {
    debugger;
    status = this.selectedAction.toLowerCase();
    if (val) {
      val = val.toLowerCase();
    } else {
      this.filerDataWithStatus(status);
    }
    const rows = this.companies.filter(function (d) {
      return (status === 'all' ? d.name.toString().toLowerCase().includes(val) :
        d.name.toString().toLowerCase().includes(val) && d.status.toLowerCase() === status);
    });
    this.filteredCompanies = rows;
    this.collectionSize = this.filteredCompanies.length;
  }

  filerDataWithStatus(val) {
  debugger;
    if (val) {
      val = val.toLowerCase();
    } else {
      return this.filteredCompanies = [...this.companies];
    }
    const rows = this.companies.filter(function (d) {
      debugger;
      return d.status.toString().toLowerCase() === val;
    });
    if (val === 'all') {
      this.filteredCompanies = this.companies;
    } else {
      this.filteredCompanies = rows;
    }
    this.collectionSize = this.filteredCompanies.length;

  }

  getRetailerCompanyByid(companyId) {
    this.companyService.getRetailerCompanyById(companyId)
      .subscribe(res => {

      }, err => {
        console.log(err);
      });
  }

  filter(action) {
    this.selectedAction = action;
    this.filerDataWithStatus(action.toString());
  }

  add() {
    this.router.navigate(['add'], { relativeTo: this.route });
  }

  setAdvancedSearchForm() {
    this.advancedSearchForm = this.fb.group({
      name: '',
      registrationNumber: '',
      status: ''
    });
  }

  open(content) {
    this.modalService.open(content, { ariaLabelledBy: 'modal-basic-title' })
    .result.then((result) => {
      console.log(result);
    }, (reason) => {
      console.log('Err!', reason);
    });
  }

  performAdvancedSearch(event) {
    this.loadingCollection = true;
    this.modalService.dismissAll('close clicked');
    this.advancedSearchApplied = true;
    // const company: RetailerCompany = this.advancedSearchForm.value;
    // this.companyService.performAdvancedSearch(company)
    //   .subscribe(res => {
    //     this.filteredCompanies = res;this.loadingCollection = false;
    //   },
    //   error => {
    //     console.log(error);
    //   });
  }

  clearSearchCriteria() {
    this.advancedSearchApplied = false;
    this.filerData('');
  }

  showPagination() {
    if (this.filteredCompanies) {
      return this.filteredCompanies.length > 6 ? true : false;
    }
    return false;
  }

}
