import { Component, Input, OnDestroy, OnInit, ViewChild, ViewChildren } from '@angular/core';
import { NgForm } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { PerfectScrollbarDirective } from 'ngx-perfect-scrollbar';
import { Subscription } from 'rxjs';
import { SharedAnimations } from 'src/app/shared/animations/shared-animations';
import { RetailerCompany } from 'src/app/shared/models/retailer-company';
import { Audit, AuditService } from 'src/app/shared/services/audit.service';
import { RetailerCompanyService } from 'src/app/shared/services/retailer-company.service';



@Component({
  selector: 'app-retailer-company-content',
  templateUrl: './retailer-company-content.component.html',
  styleUrls: ['./retailer-company-content.component.scss'],
  animations: [SharedAnimations]
})
export class RetailerCompanyContentComponent implements OnInit, OnDestroy {
  activeCompany: RetailerCompany = new RetailerCompany();

  loadAuditDataSub: Subscription;
  companySelectSub: Subscription;
  companyId: number;
  showHistory = true;
  maskedPrimPhNo: string;
  maskedSecPhNo: string;
  audits: Audit[];


  // tslint:disable-next-line:no-input-rename
  @Input('matSidenav') matSidenav;
  @ViewChild(PerfectScrollbarDirective) psContainer: PerfectScrollbarDirective;

  @ViewChildren('msgInput') msgInput;
  @ViewChild('msgForm') msgForm: NgForm;

  constructor(public companyService: RetailerCompanyService,
    private auditService: AuditService,
    private router: Router,
    private route: ActivatedRoute) {

  }

  ngOnInit() {
    this.route.params.subscribe(params => this.companyId = params['id']);

    // Listen for company change
    this.companySelectSub = this.companyService.onRetailerCompanySelected.subscribe(res => {
      if (res) {
        this.activeCompany = res.company;
        this.maskedPrimPhNo = this.formatPhoneNumber(this.activeCompany.primPhNo);
        this.maskedSecPhNo = this.formatPhoneNumber(this.activeCompany.secPhNo);
        this.showHistory = false;
      }
    });

    this.loadAuditDataSub = this.auditService.loadAuditData()
      .subscribe(res => {
        this.audits = this.auditService.audits;
      });

  }
  formatPhoneNumber(phNo: string) {
    var res = '-';
    if (phNo && phNo.length === 10) {
      res = '(';
      res += phNo.substr(0, 3);
      res += ') ';
      res += phNo.substr(3, 3);
      res += '-';
      res += phNo.substr(6);
    }

    return res;
  }


  ngOnDestroy() {
    if (this.companySelectSub) { this.companySelectSub.unsubscribe(); }
  }

  edit(company) {
    this.router.navigate(['edit', company.id], { relativeTo: this.route });
  }

  toggleShowAudit() {
    this.showHistory = !this.showHistory;
    if (this.showHistory) {
      this.loadAuditDataSub = this.auditService.loadAuditData()
        .subscribe(res => {
          this.audits = this.auditService.audits;
        });
    }
  }
}
