import { Component, OnInit } from '@angular/core';

import { Router, ActivatedRoute } from '@angular/router';
import { Action } from 'src/app/shared/common/action';
import { RetailerCompanyService } from 'src/app/shared/services/retailer-company.service';

@Component({
  selector: 'app-edit',
  templateUrl: './edit.component.html',
  styleUrls: ['./edit.component.scss']
})
export class EditComponent implements OnInit {

  companyId: number;
  action: Action = Action.EDIT;

  constructor(public companyService: RetailerCompanyService,
    private router: Router,
    private route: ActivatedRoute) {

    }

  ngOnInit() {
    this.route.params.subscribe(params => this.companyId = params['id']);
  }
}
